@use "global" as *;

.c-breadcrumb__list {
  padding-top: myrem(19);
  padding-left: myrem(3);
  display: flex;
  align-items: center;
  column-gap: myrem(25);
  @include mq(md) {
    padding-top: myrem(30);
    padding-left: myrem(99);
    column-gap: myrem(31);
  }
}

.c-breadcrumb__item {
  position: relative;
  font-size: myrem(12);
  @include mq(md) {
    font-size: myrem(16);
  }
}

.c-breadcrumb__item:not(:first-child)::before {
  content: "";
  position: absolute;
  display: inline-block;
  width: myrem(10);
  height: myrem(10);
  top: 50%;
  left: myrem(-20);
  border-top: 1px solid $black2;
  border-right: 1px solid $black2;
  transform: translateY(-50%) rotate(45deg);
  @include mq(md) {
    left: myrem(-25);
  }
}
