@use "global" as *;

.p-footer {
  padding: myrem(77) 0 myrem(55);
  background-color: $blue4;
}

.p-footer__head {
  @include mq(md) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.p-footer__logo {
  font-size: myrem(22);
  font-weight: 100;
  text-align: center;
  letter-spacing: myrem(2.3);
  @include mq(md) {
    font-size: myrem(19);
    letter-spacing: myrem(2);
  }
}

.p-footer__nav {
  padding-top: 33px;
  @include mq(md) {
    padding-top: 0;
  }
}

.p-footer__nav-list {
  @include mq(md) {
    display: flex;
  }
}

.p-footer__nav-item {
  padding-bottom: myrem(1);
  border-bottom: 1px solid $blue1;
  @include mq(md) {
    letter-spacing: myrem(1.5);
    border-bottom: none;
  }
}

.p-footer__nav-item:not(:first-child) {
  margin-top: myrem(22);
  @include mq(md) {
    margin-top: 0;
    margin-left: myrem(35);
  }
}

.p-footer__nav-item a:not(.p-footer__sub-item a) {
  @include mq(md) {
    text-decoration: underline;
    text-decoration-thickness: myrem(1);
    text-decoration-color: $blue1;
    text-underline-offset: myrem(11);
    text-decoration-skip-ink: none;
  }
}

.p-footer__nav-no-border {
  border-bottom: none;
}

.p-footer__sub-list {
  padding: 0 myrem(16) myrem(4) myrem(16);
  border-top: 1px solid $blue1;
  @include mq(md) {
    padding: 0 myrem(3) 0 myrem(3);
    border-top: none;
  }
}

.p-footer__sub-item {
  padding: myrem(15) 0 myrem(4);
  position: relative;
  font-size: myrem(14);
  border-bottom: 1px solid $gray2;
  @include mq(md) {
    padding: myrem(23) 0 0;
    border-bottom: 0;
    line-height: 0.5;
  }
}

.p-footer__sub-item::after {
  position: absolute;
  content: "";
  width: myrem(7);
  height: myrem(7);
  top: 54%;
  right: 0;
  border: 1px solid;
  border-color: $black $black transparent transparent;
  transform: translateY(-50%) rotate(45deg);
  @include mq(md) {
    top: 88%;
  }
}

.p-footer__bottom {
  margin-top: myrem(44);
  text-align: center;
  @include mq(md) {
    margin-top: myrem(74);
  }
}

.p-footer__bottom small {
  font-size: myrem(12);
  letter-spacing: myrem(0.4);
  @include mq(md) {
    letter-spacing: myrem(1.2);
  }
}
