@use "global" as *;

.swiper {
  margin-top: myrem(25);
  position: relative;
  @include mq(md) {
    margin-top: myrem(37);
    padding-bottom: myrem(60);
  }
}

.swiper-wrapper {
  width: 100%;
}

.swiper-slide {
  transition: 0.7s;
  @include mq(md) {
    transform: scale(0.8);
  }
}

.swiper-slide-active {
  transform: scale(1);
  z-index: 1;
}

.p-swiper__image {
  max-width: myrem(435);
  width: 100%;
  @include mq(md) {
    width: 435px;
  }
}

.p-swiper__image img {
  border-radius: myrem(20);
  aspect-ratio: 244/239;
  @include mq(md) {
    aspect-ratio: 435/423;
  }
}

/* 前へ次への矢印カスタマイズ */
.swiper-button-prev,
.swiper-button-next {
  width: myrem(40);
  height: myrem(40);
  background-color: $blue1;
  border-radius: 50%;
  @include mq(md) {
    width: myrem(50);
    height: myrem(50);
  }
}

.swiper-button-prev {
  top: 51%;
  left: myrem(15);
  @include mq(md) {
    top: 94%;
    right: myrem(258);
    left: auto;
  }
}

.swiper-button-next {
  top: 51%;
  right: myrem(15);
  @include mq(md) {
    top: 94%;
    right: myrem(200);
  }
}

.swiper-button-prev:after,
.swiper-button-next:after {
  content: "";
  width: myrem(12);
  height: myrem(12);
  border: 0;
  border-top: 2px solid $white;
  border-right: 2px solid $white;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.swiper-button-prev:after {
  transform: rotate(-135deg);
}

.swiper-button-next:after {
  transform: rotate(45deg);
}
