@use "global" as *;

.p-access {
  margin-top: myrem(65);
  padding-bottom: myrem(63);
  position: relative;
  @include mq(md) {
    margin-top: myrem(76);
    padding-bottom: myrem(107);
  }
}

.p-access__title {
  text-align: center;
}

.p-access__sub-title {
  margin-top: myrem(-5);
  text-align: center;
  @include mq(md) {
    margin-top: myrem(-9);
  }
}

.p-access iframe {
  margin-top: myrem(25);
  width: 100%;
  height: myrem(195);
  border-radius: myrem(50);
  @include mq(md) {
    margin-top: myrem(38);
    height: myrem(499);
  }
}

.p-access__box {
  margin-top: myrem(10);
  display: flex;
  flex-direction: column;
  row-gap: myrem(37);
  @include mq(md) {
    margin-top: myrem(48);
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    column-gap: myrem(112);
  }
}

.p-access__information-box {
  @include mq(md) {
    margin: 0 auto;
  }
}

.p-access__name {
  font-size: myrem(24);
  font-weight: 100;
  letter-spacing: myrem(2.5);
  @include mq(md) {
    font-size: myrem(32);
    letter-spacing: myrem(3.3);
  }
}

.p-access__tel {
  margin-top: myrem(5);
  font-size: myrem(19);
  font-weight: bold;
  @include mq(md) {
    margin-top: myrem(13);
    font-size: myrem(22);
  }
}

.p-access__tel-number {
  margin-left: myrem(-3);
  font-size: myrem(30);
  letter-spacing: myrem(0.1);
  @include mq(md) {
    font-size: myrem(35);
  }
}

.p-access__address,
.p-access__time,
.p-access__close {
  font-size: myrem(12);
  @include mq(md) {
    font-size: myrem(14);
  }
}

.p-access__address {
  margin-top: myrem(7);
  letter-spacing: myrem(0.5);
  @include mq(md) {
    margin-top: myrem(14);
    letter-spacing: myrem(0.6);
  }
}

.p-access__time {
  margin-top: myrem(3);
  letter-spacing: myrem(0.7);
  @include mq(md) {
    margin-top: myrem(7);
  }
}

.p-access__close {
  margin-top: myrem(4);
  letter-spacing: myrem(0.8);
  @include mq(md) {
    margin-top: myrem(8);
  }
}

.p-access__schedule-box {
  width: 100%;
  font-size: myrem(14);
  @include mq(md) {
    margin: 0 auto;
    max-width: myrem(500);
    display: flex;
    align-items: end;
    font-size: myrem(16);
    line-height: 1.3;
  }
}

.p-access__schedule {
  display: block;
  border-collapse: collapse;
}

.p-access__schedule-title {
  padding: myrem(6) myrem(8) myrem(8);
  border-top: 1px solid $black;
  font-weight: 400;
  @include mq(md) {
    padding: myrem(6) myrem(18) myrem(8);
  }
}

.p-access__schedule-title-sun {
  padding: 0;
  @include mq(md) {
    padding: myrem(6) myrem(16) myrem(8);
  }
}

.p-access__schedule-title,
.p-access__schedule-text {
  text-align: center;
  border-bottom: 1px solid $black;
}

.p-access__schedule-text {
  padding: myrem(7) 0 myrem(3);
  @include mq(md) {
    padding: myrem(17) 0 myrem(13);
  }
}

.p-access__schedule-hour {
  width: 13%;
  @include mq(md) {
    padding: 0 13px;
    width: auto;
  }
}

.p-access__button {
  margin-top: myrem(42);
  text-align: center;
  @include mq(md) {
    margin-top: myrem(48);
    margin-left: myrem(-26);
  }
}
