@use "global" as *;

.p-flow {
  margin-top: myrem(59);
  @include mq(md) {
    margin-top: myrem(100);
  }
}

.p-flow__container {
  display: flex;
  flex-direction: column;
  row-gap: myrem(25);
  @include mq(md) {
    flex-direction: row;
  }
}

.p-flow__title {
  text-align: center;
  letter-spacing: myrem(2.6);
  @include mq(md) {
    text-align: start;
  }
}

.p-flow__sub-title {
  text-align: center;
  @include mq(md) {
    margin-top: myrem(-8);
    text-align: start;
  }
}

.p-flow__list {
  margin: 0 auto;
  max-width: myrem(345);
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  column-gap: myrem(5);
  row-gap: myrem(29);
  @include mq(md) {
    max-width: myrem(803);
    margin-top: myrem(10);
    margin-left: myrem(24);
    flex-wrap: nowrap;
    justify-content: center;
    column-gap: myrem(1.5);
  }
}

.p-flow__item {
  padding: myrem(30) 0 myrem(26);
  max-width: myrem(152);
  width: 100%;
  background-color: $blue1;
  border-radius: 50%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @include mq(md) {
    max-width: myrem(180);
    padding-top: myrem(37);
  }
}

.p-flow__item2 {
  padding: myrem(37) 0 myrem(29);
}

.p-flow__item3 {
  padding: myrem(30) 0 myrem(28);
}

.p-flow__number {
  padding: myrem(8) 0;
  width: myrem(42);
  max-width: 100%;
  position: absolute;
  top: myrem(-3);
  left: myrem(-1);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: $blue1;
  background-color: $gray;
  border-radius: 50%;
  @include mq(md) {
    width: myrem(50);
    font-size: myrem(20);
  }
}

.p-flow__image1 {
  max-width: myrem(60.86);
  width: 100%;
  aspect-ratio: 60.86/52.98;
  @include mq(md) {
    max-width: myrem(72.34);
    aspect-ratio: 72.34/62.98;
  }
}

.p-flow__image2 {
  max-width: myrem(65.77);
  width: 100%;
  aspect-ratio: 65.77/50.17;
  @include mq(md) {
    max-width: myrem(78.18);
    aspect-ratio: 78.18/59.63;
  }
}

.p-flow__image3 {
  max-width: myrem(46.79);
  width: 100%;
  aspect-ratio: 46.79/62.8;
  @include mq(md) {
    max-width: myrem(55.62);
    aspect-ratio: 55.62/74.65;
  }
}

.p-flow__image4 {
  max-width: myrem(51.92);
  width: 100%;
  aspect-ratio: 51.92/53.57;
  @include mq(md) {
    max-width: myrem(61.72);
    aspect-ratio: 61.72/63.68;
  }
}

.p-flow__text {
  color: $white;
  font-size: myrem(11);
  font-weight: 600;
  @include mq(md) {
    font-size: myrem(15);
    line-height: 1.5;
  }
}

.p-flow__text1 {
  margin-top: myrem(5);
  margin-left: myrem(-13);
  text-align: center;
  @include mq(md) {
    margin-top: myrem(7);
    margin-left: myrem(-4);
  }
}

.p-flow__text2 {
  margin-top: myrem(18);
  @include mq(md) {
    margin-top: myrem(25);
  }
}

.p-flow__text3 {
  margin-top: myrem(11);
  @include mq(md) {
    margin-top: myrem(17);
  }
}

.p-flow__text4 {
  margin-top: myrem(3);
  margin-left: myrem(-13);
  text-align: center;
  @include mq(md) {
    margin-top: myrem(5);
    margin-left: myrem(-7);
  }
}

.p-flow__arrow-box {
  width: 4%;
  display: flex;
  align-items: center;
  justify-content: center;
  @include mq(md) {
    width: 2.5%;
  }
}

.p-flow__arrow-box3 {
  width: 3.5%;
  @include mq(md) {
    width: 3%;
  }
}

.p-flow__arrow {
  border-top: myrem(13) solid transparent;
  border-bottom: myrem(13) solid transparent;
  border-left: myrem(13) solid $blue1;
  @include mq(md) {
    border-top: myrem(16) solid transparent;
    border-bottom: myrem(16) solid transparent;
    border-left: myrem(16) solid $blue1;
  }
}
