@use "global" as *;

.p-news {
  margin-top: myrem(35);
  @include mq(md) {
    margin-top: 0;
  }
}

.p-news__box {
  @include mq(md) {
    padding-top: myrem(283);
    display: flex;
    justify-content: end;
    column-gap: myrem(36);
  }
}

.p-news__section-box {
  @include mq(md) {
    width: 40%;
    text-align: end;
  }
}

.p-news__sub-title {
  margin-top: myrem(-8);
  color: $purple1;
  @include mq(md) {
    width: 74%;
  }
}

.p-news__items {
  padding-top: myrem(19);
  @include mq(md) {
    max-width: myrem(623);
    width: 100%;
  }
}

.p-news__item {
  font-size: myrem(14);
  display: flex;
  flex-direction: column;
  @include mq(md) {
    padding-bottom: myrem(5);
    border-bottom: 1px solid $purple1;
    flex-direction: row;
    align-items: baseline;
  }
}

.p-news__item:not(:first-child) {
  padding-top: myrem(3);
  @include mq(md) {
    padding-top: myrem(16);
  }
}

.p-news__date {
  letter-spacing: myrem(0.5);
}

.p-news__body {
  margin-top: myrem(-5);
  padding-bottom: myrem(10);
  display: flex;
  border-bottom: 1px solid $purple1;
  letter-spacing: myrem(0.5);
  @include mq(md) {
    padding-left: myrem(13);
    padding-bottom: 0;
    border-bottom: none;
  }
}

.p-news__body-text {
  padding-left: myrem(14);
}

.p-news__more {
  padding-top: myrem(6);
  padding-right: myrem(24);
  font-size: myrem(14);
  text-align: end;
  @include mq(md) {
    padding-top: myrem(18);
    font-size: myrem(16);
  }
}
