@use "global" as *;

.c-section-title {
  font-size: myrem(28);
  font-weight: 400;
  letter-spacing: myrem(1.9);
  @include mq(md) {
    font-size: myrem(32);
    letter-spacing: myrem(3);
  }
}

.c-section-subtitle {
  font-size: myrem(14);
  color: $blue1;
  @include mq(md) {
    font-size: myrem(18);
  }
}
