@use "global" as *;

.p-company {
  margin-top: myrem(136);
  padding-bottom: myrem(80);
  @include mq(md) {
    margin-top: myrem(209);
    padding-bottom: myrem(114);
  }
}

.p-company__title {
  text-align: center;
}

.p-company__sub-title {
  margin-top: myrem(-6);
  letter-spacing: myrem(0.5);
  text-align: center;
  @include mq(md) {
    margin-top: myrem(-9);
    letter-spacing: myrem(1.8);
  }
}

.p-company__about {
  margin-top: myrem(33);
  width: 100%;
  border-collapse: collapse;
  @include mq(md) {
    margin-top: myrem(38);
  }
}

.p-company__row {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid $black2;
  @include mq(md) {
    flex-direction: row;
  }
}

.p-company__row:first-child {
  border-top: 1px solid $black2;
}

.p-company__row-desktop {
  display: none;
  @include mq(md) {
    display: flex;
  }
}

.p-company__table-title,
.p-company__text {
  font-size: myrem(14);
  font-weight: 400;
  text-align: center;
  @include mq(md) {
    font-size: myrem(16);
  }
}

.p-company__table-title {
  padding: myrem(8) 0;
  background-color: $gray;
  @include mq(md) {
    max-width: 100%;
    width: myrem(314);
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.p-company__text {
  padding: myrem(8) myrem(18) myrem(17);
  letter-spacing: myrem(1.4);
  @include mq(md) {
    width: calc(100% - myrem(314));
    padding: myrem(17) myrem(30) myrem(18);
    text-align: start;
    letter-spacing: myrem(1.6);
  }
}

.p-company__text-style {
  padding-bottom: myrem(8);
  line-height: 1.6;
  @include mq(md) {
    padding-bottom: myrem(14);
  }
}

.p-company__text-layout {
  display: flex;
  justify-content: center;
  column-gap: myrem(4);
  @include mq(md) {
    flex-direction: column;
  }
}

.p-company__text a {
  text-decoration: underline;
}
