@use "global" as *;

.p-about-top {
  margin-top: myrem(141);
  text-align: center;
  @include mq(md) {
    position: relative;
    margin-top: myrem(118);
  }
}

.p-about-top__box {
  display: flex;
  flex-direction: column;
  row-gap: myrem(28);
  @include mq(md) {
    flex-direction: row;
    flex-wrap: wrap;
    column-gap: myrem(40);
    row-gap: myrem(60);
  }
}

.p-about-top__image-box {
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  @include mq(md) {
    max-width: myrem(460);
  }
}

.p-about-top__image1 {
  max-width: myrem(208);
  width: 100%;
  @include mq(md) {
    max-width: myrem(353);
  }
}

.p-about-top__image1 img {
  border-radius: myrem(15);
  aspect-ratio: 208/245;
  @include mq(md) {
    border-radius: myrem(30);
    aspect-ratio: 353/414;
  }
}

.p-about-top__image2 {
  margin-top: myrem(-91);
  margin-left: myrem(144);
  max-width: myrem(202);
  width: 100%;
  position: static;
  z-index: -1;
  @include mq(md) {
    margin-top: myrem(-40);
    margin-left: myrem(116);
    max-width: myrem(344);
    top: myrem(375);
  }
}

.p-about-top__image2 img {
  border-radius: myrem(15);
  aspect-ratio: 202/145;
  @include mq(md) {
    border-radius: myrem(30);
    aspect-ratio: 344/246;
  }
}

.p-about-top__text-box {
  @include mq(md) {
    margin: 0 auto 0;
    max-width: myrem(500);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: end;
  }
}

.p-about-top__title {
  letter-spacing: myrem(3);
  text-align: start;
}

.p-about-top__sub-title {
  margin-top: myrem(-6);
  text-align: start;
  @include mq(md) {
    letter-spacing: myrem(2);
  }
}

.p-about-top__text {
  padding-top: myrem(25);
  padding-bottom: myrem(38);
  font-size: myrem(14);
  letter-spacing: myrem(0.4);
  line-height: 2.1;
  text-align: start;
  @include mq(md) {
    padding-top: myrem(30);
    padding-bottom: myrem(56);
  }
}

// about-page
// 私たちについて-----------------------------------------------------------------

.p-about {
  margin-top: myrem(100);
  @include mq(md) {
    margin-top: myrem(180);
    padding: 0 myrem(100);
  }
}

.p-about__container {
  margin: 0 auto;
  max-width: myrem(520);
  width: 100%;
  padding-right: myrem(15);
  padding-left: myrem(15);
  @include mq(md) {
    padding: 0;
    max-width: myrem(1200);
  }
}

.p-about__wrapper {
  margin: 0 auto;
  padding: 0 myrem(15) myrem(15);
  max-width: myrem(520);
  width: 100%;
  background-color: $white;
  border-radius: myrem(20);
  @include mq(md) {
    padding: 0;
    max-width: myrem(1200);
  }
}

.p-about__title {
  padding-top: myrem(26);
  letter-spacing: myrem(0.5);
  text-align: center;
  @include mq(md) {
    padding-top: myrem(33);
    letter-spacing: myrem(3);
  }
}

.p-about__sub-title {
  margin-top: myrem(-6);
  letter-spacing: myrem(0.5);
  text-align: center;
  @include mq(md) {
    letter-spacing: myrem(1.8);
  }
}

.p-about__head {
  padding-top: myrem(22);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @include mq(md) {
    padding: myrem(35) myrem(50) 0;
    flex-direction: row;
    column-gap: myrem(30);
  }
}

.p-about__image {
  max-width: myrem(315);
  width: 100%;

  @include mq(md) {
    max-width: myrem(485);
  }
}

.p-about__image img {
  aspect-ratio: 315/188;
  border-radius: myrem(15);

  @include mq(md) {
    border-radius: myrem(20);
    aspect-ratio: 485/290;
  }
}

.p-about__text-box {
  padding: myrem(18) 0;
  font-size: myrem(14);
  @include mq(md) {
    max-width: myrem(485);
    width: 100%;
    padding: 0;
  }
}

.p-about__text {
  letter-spacing: myrem(0.1);
  line-height: 2.1;
  @include mq(md) {
    margin-top: myrem(17);
  }
}

.p-about__name {
  margin-top: myrem(15);
  text-align: end;
}

.p-about__name--large {
  margin-left: myrem(13);
  font-size: myrem(22);
  letter-spacing: myrem(5.1);
}

// 略歴-----------------------------------------
.p-about__bottom-box {
  @include mq(md) {
    margin: 0 auto;
    padding: myrem(42) myrem(50) 0;
    max-width: myrem(1100);
  }
}

.p-about__bottom {
  font-size: myrem(14);
  @include mq(md) {
    display: flex;
    column-gap: myrem(32);
  }
}

.p-about__biography-box {
  padding-right: myrem(15);
  padding-left: myrem(15);
  @include mq(md) {
    padding-left: 0;
    padding-right: myrem(0);
    width: calc(100% - myrem(32));
  }
}

.p-about__biography-title,
.p-about__qualification-box-title {
  padding: myrem(5) myrem(36);
  width: fit-content;
  font-size: myrem(18);
  color: $white;
  background-color: $blue1;
  border-radius: myrem(8) myrem(8) 0 0;
  @include mq(md) {
    font-size: myrem(22);
  }
}

.p-about__biography-text {
  padding: myrem(14) 0;
  line-height: 1.5;
  border-bottom: 1px solid $blue1;
  @include mq(md) {
    padding: myrem(19) 0;
  }
}

.p-about__biography-text-border {
  border-top: 2px solid $blue1;
}

.p-about__qualification-text-border {
  border-top: 2px solid $blue1;
  @include mq(md) {
    border-top: none;
  }
}

.p-about-biography-text-break::after {
  content: "\A";
  white-space: pre;
  @include mq(md) {
    white-space: unset;
  }
}

.p-about__qualification-box {
  margin-top: myrem(31);
  padding-right: myrem(15);
  padding-left: myrem(15);
  @include mq(md) {
    margin-top: 0;
    padding-right: 0;
    padding-left: 0;
    width: calc(100% - myrem(32));
  }
}

.p-about__qualification-box-title {
  padding: myrem(5) myrem(17);
}

.p-about__qualification {
  @include mq(md) {
    padding: myrem(19) 0 myrem(4);
    display: flex;
    border-top: 2px solid $blue1;
    border-bottom: 1px solid $blue1;
  }
}

.p-about__qualification-title {
  padding: myrem(12) 0 myrem(5);
  @include mq(md) {
    padding: 0;
    width: 40%;
  }
}

.p-about__qualification-list,
.p-about__conference-list {
  padding-bottom: myrem(15);
  border-bottom: 1px solid $blue1;
  @include mq(md) {
    width: 100%;
    border-bottom: none;
  }
}

.p-about__qualification-item-underline {
  position: relative;
}

.p-about__qualification-item-underline::after {
  position: absolute;
  content: "";
  background-color: $black;
  width: myrem(120);
  height: myrem(1);
  bottom: myrem(4);
  left: myrem(15);
}

.p-about__qualification-item-underline2 {
  position: relative;
}

.p-about__qualification-item-underline2::after {
  position: absolute;
  content: "";
  background-color: $black;
  width: myrem(180);
  height: myrem(1);
  bottom: myrem(3);
  left: myrem(15);
}

.p-about__conference {
  @include mq(md) {
    padding: myrem(19) 0 myrem(4);
    display: flex;
    border-bottom: 1px solid $blue1;
  }
}

.p-about__conference-title {
  padding: myrem(18) 0 myrem(5);
  @include mq(md) {
    width: 40%;
    padding: 0;
  }
}
