@use "global" as *;

.p-main-catch__container {
  padding-top: myrem(20);
  display: flex;
  flex-direction: column;
  row-gap: myrem(160);
  @include mq(md) {
    padding-top: myrem(41);
    row-gap: myrem(57);
  }
}

.p-main-catch__image {
  max-width: myrem(314);
  width: 100%;
  position: absolute;
  right: 0;
  z-index: -1;
  @include mq(md) {
    max-width: myrem(1000);
  }
}

.p-main-catch__image img {
  border-radius: myrem(30) 0 0 myrem(30);
  aspect-ratio: 314/232;
  @include mq(md) {
    border-radius: myrem(50) 0 0 myrem(50);
    aspect-ratio: 1000/593;
  }
}

.p-main-catch__top {
  font-size: myrem(18);
  letter-spacing: myrem(0.1);
  color: $blue2;
  font-family: $font-sub;
  transform: rotate(-7deg);
  @include mq(md) {
    margin-left: myrem(-81);
    font-size: myrem(52);
  }
}

.p-main-catch__text {
  padding: myrem(4) myrem(18) myrem(1) myrem(8);
  width: fit-content;
  font-size: myrem(22);
  letter-spacing: myrem(2);
  color: $white;
  background-color: $blue2;
  @include mq(md) {
    font-size: myrem(34);
    padding-top: myrem(2);
    padding-right: myrem(8);
    padding-bottom: myrem(1);
    padding-left: myrem(7);
    letter-spacing: myrem(3);
  }
}

.p-main-catch__text:not(:first-child) {
  margin-top: myrem(3);
  @include mq(md) {
    margin-top: myrem(7);
  }
}

.p-main-catch__text-sub {
  padding-top: myrem(9);
  font-size: myrem(15);
  letter-spacing: myrem(0.6);
  @include mq(md) {
    margin-top: myrem(4);
    font-size: myrem(20);
    letter-spacing: myrem(0.7);
  }
}
