@use "global" as *;

.p-float {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  z-index: 100;
  @include mq(md) {
    display: none;
  }
}

.p-float__tel {
  padding: myrem(16) 0;
  width: 100%;
  font-size: myrem(26);
  font-weight: bold;
  color: $black2;
  text-align: center;
  background-color: $white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.p-float__tel-inner {
  display: flex;
  align-items: center;
  justify-content: center;
}

.p-float__tel-image {
  margin-right: myrem(13);
  width: myrem(16);
  display: inline-block;
}

.p-float__tel-text {
  font-size: myrem(19);
  font-weight: bold;
  display: inline-block;
  letter-spacing: myrem(0.2);
}

.p-float__tel-number {
  display: inline-block;
}

.p-float__reservation {
  width: 100%;
  letter-spacing: myrem(2);
  color: $white;
  background-color: $blue2;
  display: flex;
  align-items: center;
  justify-content: center;
}

.p-float__reservation-inner {
  display: flex;
  align-items: center;
}

.p-float__reservation-image {
  margin-right: myrem(24);
  width: myrem(29);
}

.p-float__reservation-text {
  font-size: myrem(17);
  font-weight: 500;
  letter-spacing: myrem(2);
}
