@use "global" as *;

.p-facility {
  margin-top: myrem(79);
  padding-top: myrem(57);
  position: relative;
  @include mq(md) {
    margin-top: myrem(108);
    padding-top: myrem(98);
  }
}

.p-facility__title {
  text-align: center;
}

.p-facility__sub-title {
  text-align: center;
  @include mq(md) {
    margin-top: myrem(-7);
  }
}

.p-facility__cards {
  margin-top: myrem(32);
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  row-gap: myrem(26);
  column-gap: myrem(30);
  @include mq(md) {
    margin-top: myrem(36);
    grid-template-columns: repeat(6, 1fr);
    row-gap: myrem(31);
  }
}

.p-facility__card {
  margin: 0 auto;
  @include mq(md) {
    max-width: myrem(313);
  }
}

@include mq(md) {
  .p-facility__card:nth-child(1) {
    grid-area: 1 / 1 / 2 / 3;
  }
  .p-facility__card:nth-child(2) {
    grid-area: 1 / 3 / 2 / 5;
  }
  .p-facility__card:nth-child(3) {
    grid-area: 1 / 5 / 2 / 7;
  }
  .p-facility__card:nth-child(4) {
    grid-area: 2 / 2 / 3 / 4;
  }
  .p-facility__card:nth-child(5) {
    grid-area: 2 / 4 / 3 / 6;
  }
}

.p-facility__card-image img {
  border-radius: myrem(15);
  aspect-ratio: 344/204;
  @include mq(md) {
    border-radius: myrem(20);
    aspect-ratio: 313/186;
  }
}

.p-facility__card-body {
  padding-top: myrem(19);
  padding-bottom: myrem(11);
  @include mq(md) {
    padding-top: myrem(18);
    padding-bottom: myrem(6);
  }
}

.p-facility__card-title {
  font-size: myrem(21);
  letter-spacing: myrem(3);
  @include mq(md) {
    font-size: myrem(20);
  }
}

.p-facility__card-text {
  margin-top: myrem(9);
  padding-right: myrem(3);
  letter-spacing: myrem(0.4);
  @include mq(md) {
    margin-top: myrem(6);
    padding-right: 0;
  }
}
