@use "global" as *;

.p-header {
  padding-right: myrem(10);
  width: 100%;
  height: myrem(60);
  position: fixed;
  top: 0;
  left: 0;
  background-color: $white;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include mq(md) {
    padding-right: 0;
    height: myrem(80);
  }
}

.p-header__logo {
  padding-left: myrem(16);
  font-size: myrem(16);
  font-weight: 100;
  letter-spacing: myrem(1.6);
  @include mq(md) {
    padding-left: myrem(102);
    font-size: myrem(28);
    letter-spacing: myrem(2.8);
    line-height: 1.2;
  }
}

.p-header__information-box {
  display: none;
  padding-top: myrem(38);
  padding-right: myrem(29);
  padding-left: myrem(31);
  position: fixed;
  top: 60px;
  z-index: 100;
  width: 100vw;
  height: calc(100vh - 60px);
  background-color: $white;

  @include mq(md) {
    padding-top: 0;
    padding-right: 0;
    padding-left: 0;
    width: fit-content;
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    position: unset;
  }
}

.p-header__information {
  @include mq(md) {
    display: flex;
    flex-direction: row-reverse;
  }
}

.p-header__reservation {
  margin: 0 auto;
  padding-top: myrem(15);
  padding-bottom: myrem(18);
  max-width: myrem(315);
  width: 100%;
  font-size: myrem(22);
  font-weight: 500;
  letter-spacing: myrem(2);
  color: $white;
  background-color: $blue2;
  border-radius: myrem(10);
  @include mq(md) {
    max-width: myrem(120);
    font-size: myrem(18);
    border-radius: 0;
  }
}

.p-header__reservation-inner {
  display: flex;
  align-items: center;
  @include mq(md) {
    justify-content: center;
  }
}

.p-header__reservation-image {
  margin-right: myrem(20);
  margin-left: myrem(78);
  width: myrem(29);
  @include mq(md) {
    display: none;
  }
}

.p-header__reservation-text {
  @include mq(md) {
    line-height: 2.7;
  }
}

.p-header__tel {
  margin: myrem(11) auto 0;
  padding-bottom: myrem(9);
  max-width: myrem(315);
  width: 100%;
  font-size: myrem(26);
  font-weight: bold;
  color: $black2;
  text-align: center;
  background-color: rgb(164, 221, 224, 0.3);
  border-radius: myrem(10);
  @include mq(md) {
    padding-top: myrem(13);
    max-width: 100%;
    width: myrem(298);
    margin-top: 0;
    display: flex;
    flex-direction: column;
    border-radius: 0;
  }
}

.p-header__tel-image {
  width: myrem(16);
  display: inline-block;
  @include mq(md) {
    display: none;
  }
}

.p-header__tel-text {
  font-size: myrem(26);
  font-weight: bold;
  display: inline-block;
  @include mq(md) {
    letter-spacing: 0;
    line-height: 1;
  }
}

.p-header__tel-small-text {
  font-size: myrem(16);
  display: none;
  @include mq(md) {
    margin-right: myrem(-5);
    display: inline-block;
  }
}

.p-header__tel-number {
  display: inline-block;
  @include mq(md) {
    letter-spacing: myrem(0.4);
  }
}

.p-header__opening-time {
  display: none;
  @include mq(md) {
    margin-top: myrem(-8);
    padding-right: myrem(27);
    font-size: myrem(12);
    font-weight: 400;
    display: block;
    text-align: end;
  }
}

.p-header__comment {
  margin-top: myrem(-6);
  font-size: myrem(12);
  font-weight: 400;
  letter-spacing: myrem(0.4);
  color: $black;
  @include mq(md) {
    display: none;
  }
}

.p-header__nav {
  margin: 0 auto;
  padding-top: myrem(29);
  max-width: myrem(583);
  @include mq(md) {
    margin: 0;
    padding-top: 0;
    display: flex;
    align-items: center;
  }
}

.p-header__nav-list {
  @include mq(md) {
    margin-right: myrem(30);
    display: flex;
    position: relative;
    flex-wrap: wrap;
  }
}

.p-header__nav-item {
  padding-left: myrem(9);
  padding-bottom: myrem(9);
  font-size: myrem(18);
  border-bottom: 1px solid $blue2;
  @include mq(md) {
    padding-left: myrem(24);
    padding-bottom: 0;
    border-bottom: unset;
    letter-spacing: 0;
  }
}

.p-header__nav-item a {
  display: block;
}

.p-header__nav-noborder {
  border-bottom: none;
}

.p-header__nav-arrow {
  position: relative;
}

.p-header__nav-arrow::before {
  content: "";
  display: inline-block;
  width: myrem(15);
  height: myrem(15);
  position: absolute;
  top: myrem(13);
  right: myrem(12);
  border-top: 2px solid $blue1;
  border-right: 2px solid $blue1;
  transform: rotate(-45deg);
  @include mq(md) {
    display: none;
  }
}

.p-header__nav-item a:not(.p-header__sub-item a):hover {
  color: $blue1;
}

.p-header__nav-item:not(:first-child) {
  margin-top: myrem(10);
  @include mq(md) {
    margin-top: 0;
  }
}

.p-header__nav-item:not(:last-child) {
  @include mq(md) {
    padding-right: myrem(24);
    border-right: 1px solid #000;
  }
}

.p-header__sub-list {
  @include mq(md) {
    visibility: hidden;
    width: myrem(163);
    position: absolute;
    display: inline-block;
    top: 55px;
    left: 0;
    background-color: rgb(255, 255, 255, 0.8);
    border-radius: myrem(10);
    transition: all 0.5s;
    opacity: 0;
  }
}

.p-header__sub-item a {
  display: block;
}

.p-header__nav-hover:hover .p-header__sub-list {
  visibility: visible;
  opacity: 1;
}

.p-header__sub-item {
  margin-top: myrem(12);
  padding-top: myrem(8);
  padding-left: myrem(10);
  font-size: myrem(15);
  display: flex;
  align-items: center;
  @include mq(md) {
    margin-top: 0;
    padding: myrem(12) 46px myrem(22);
    letter-spacing: myrem(2);
  }
}

.p-header__sub-item:first-child {
  border-top: 1px solid $blue2;
  @include mq(md) {
    border-top: none;
  }
}

.p-header__sub-item::before {
  margin-right: myrem(9);
  content: "";
  width: myrem(16);
  height: myrem(1);
  background-color: $blue1;
  @include mq(md) {
    display: none;
  }
}

.p-header__sub-item:not(:last-child):after {
  @include mq(md) {
    content: "";
    width: myrem(133);
    height: myrem(1);
    display: inline-block;
    background-color: rgb(72, 72, 72, 0.3);
    position: absolute;
    left: myrem(12);
    margin-top: myrem(64);
  }
}

.p-header__sub-item:hover {
  color: $black;
}

/* ハンバーガーメニュー展開時、背景を固定 */
body.active {
  height: 100%;
  overflow: hidden;
}
