@use "global" as *;

.c-button {
  padding: myrem(15) 0 myrem(14);
  width: myrem(240);
  position: relative;
  display: inline-block;
  color: $blue1;
  font-size: myrem(14);
  font-weight: 500;
  line-height: 1.4;
  text-align: center;
  border: 1px solid $blue1;
  border-radius: myrem(30);
  letter-spacing: myrem(1.3);
  outline: none;
  transition: ease 0.2s;
  overflow: hidden;
}

.c-button:hover {
  color: $white;
  outline-width: 0;
  opacity: 1;
}

.c-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background: $blue1;
  width: 100%;
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1) 0s;
  transform: scale(0, 1);
  transform-origin: right top;
}

.c-button:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}

.c-button::after {
  position: absolute;
  content: "";
  width: myrem(9);
  height: myrem(9);
  top: 50%;
  right: 8%;
  border: 1px solid;
  border-color: $blue1 $blue1 transparent transparent;
  transform: translateY(-50%) rotate(45deg);
}

.c-button:hover::after {
  border-color: $white $white transparent transparent;
  z-index: 3;
}

.c-button__inner {
  position: relative;
  z-index: 3;
}

// ボタン青--------------------------
.c-button--blue {
  color: $white;
  border: 1px solid currentColor;
  background-color: $blue1;
}

.c-button--blue:hover {
  color: $blue1;
}

.c-button--blue::before {
  background-color: $white;
}

.c-button--blue::after {
  border-color: $white $white transparent transparent;
}

.c-button--blue:hover::after {
  border-color: $blue1 $blue1 transparent transparent;
}

// ボタン紫----------------------------
.c-button--purple {
  color: $white;
  border: 1px solid currentColor;
  background-color: $purple1;
}

.c-button--purple:hover {
  color: $purple1;
}

.c-button--purple::before {
  background-color: $white;
}

.c-button--purple::after {
  border-color: $white $white transparent transparent;
}

.c-button--purple:hover::after {
  border-color: $purple1 $purple1 transparent transparent;
}

// ボタン(もっと見る)----------------------
.c-button-more {
  position: relative;
  display: inline-block;
  color: $blue1;
  font-weight: bold;
}

.c-button-more__inner {
  position: absolute;
  content: "";
  width: myrem(18);
  height: myrem(18);
  top: 50%;
  right: -30%;
  background-color: $blue1;
  border-radius: 50%;
  transform: translateY(-50%);
}

.c-button-more__inner::after {
  position: absolute;
  content: "";
  width: myrem(7);
  height: myrem(7);
  top: 50%;
  right: 40%;
  border: 1px solid;
  border-color: $white $white transparent transparent;
  transform: translateY(-50%) rotate(45deg);
}

// ボタン紫(もっと見る)----------------------
.c-button-more--purple {
  color: $purple1;
}

.c-button-more__inner--purple {
  background-color: $purple1;
}
