@use "global" as *;

.p-interior {
  margin-top: myrem(137);
  @include mq(md) {
    margin-top: myrem(247);
  }
}

.p-interior__title {
  letter-spacing: myrem(1);
  text-align: center;
  @include mq(md) {
    margin-left: myrem(21);
    letter-spacing: myrem(3);
  }
}

.p-interior__sub-title {
  margin-top: myrem(-2);
  letter-spacing: myrem(0.6);
  text-align: center;
  @include mq(md) {
    margin-top: myrem(-10);
    letter-spacing: myrem(1.8);
  }
}
